

.launch-div{
  margin: 5rem auto;
  width: 70%;
  background-color: #4099b100;
  border-radius: 15px;
}

.game-div{
  margin: auto;
  width: 60%;
  background-color: #4099b100;
  border-radius: 15px;
}
.bottom-div {
  margin: auto;
  width: 60%;
}

.score-div{
  margin: 5rem auto 1rem;
  font-family: 'Righteous', cursive;
  font-size: 2rem;
  color: white;
}

.level{
  text-align: left;
  font-family: 'Righteous', cursive;
  font-size: 1rem;
  color: white;
}
.theme{
  font-family: 'Righteous', cursive;
  font-size: 2rem;
  color: white;
}
.button{
  text-align: right;
  font-family: 'Righteous', cursive;
  font-size: 1rem;
  color: white;
}
.button:hover{
cursor: pointer;
}
@media only screen and (max-width: 800px) {
  .game-div{
    width: 90%;

  }
 }
@media only screen and (max-width: 440px) {
  .game-div{
    width: 90%;

  }
 }
.navbar {
    background: #8acaaf !important;
    box-shadow: 4px 4px rgba(53, 52, 52, 0.514);
}
img {
    width: 9rem;
    height: 9rem;
}

#image-div {
    background-color:#8acaaf;
    box-shadow: 4px 4px rgba(53, 52, 52, 0.514);
    margin: 1rem;
}
#image-div:hover{
    background-color: white;
}

@media only screen and (max-width: 440px) {
   img {
    width: 5rem;
    height: 5rem;
    }
    #image-div {

        margin: 0.3rem;
    }
  }
